import { Component, OnInit } from '@angular/core';
import { ReportsService, WeeklyReportV2 } from '../../api/reports.service';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from '../../api/auth.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'upb-weekly-report-page',
  templateUrl: './weekly-report-page.component.html',
  styleUrls: ['./weekly-report-page.component.scss']
})
export class WeeklyReportPageComponent implements OnInit {
  weeklyReport$?: Observable<WeeklyReportV2>;

  constructor(
    private reportsService: ReportsService,
    private route: ActivatedRoute,
    private authService: AuthService
  ) {}

  ngOnInit() {
    const accessToken = this.route.snapshot.queryParamMap.get('accessToken');
    const dataToken = this.route.snapshot.queryParamMap.get('dataToken');
    if (!accessToken || !dataToken) {
      this.authService.signOut();
    }
    this.weeklyReport$ = this.reportsService.getWeeklyReport(accessToken, dataToken);
  }
}
