import * as moment from 'moment-timezone';

export const formatDatePeriod = (fromDate: string, toDate: string) => {
  const fromDateMoment = moment(fromDate);
  const toDateMoment = moment(toDate);
  if (!fromDateMoment.isValid() || !toDateMoment.isValid()) {
    throw new Error('Invalid date');
  }

  const fromDateFormatted = fromDateMoment.format('MMM D - ');
  const toDateFormatted = fromDateMoment.isSame(toDateMoment, 'month')
    ? toDateMoment.format('D, YYYY')
    : toDateMoment.format('MMM D, YYYY');

  return fromDateFormatted + toDateFormatted;
};
