import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { formatDatePeriod } from '../format-date-period';
import * as moment from 'moment-timezone';
import { WeeklyReportV2 } from '../../api/reports.service';
import { SchedulingLeaderBoard } from '../../types/company';
import { orderBy } from 'lodash-es';

@Component({
  selector: 'upb-weekly-report',
  templateUrl: './weekly-report.component.html',
  styleUrls: ['./weekly-report.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WeeklyReportComponent implements OnInit {
  generatedAt = moment.tz('America/New_York').format('MMM D, YYYY @hh:mm A z');
  formatDatePeriod = formatDatePeriod;
  @Input() hidePerformanceInsights = true;
  @Input() weeklyReport?: WeeklyReportV2;
  leaderBoard: SchedulingLeaderBoard[] = [];

  ngOnInit(): void {
    this.leaderBoard = orderBy(this.weeklyReport.schedulingLeaderBoard, ['value', 'totalBooked'], ['desc', 'desc']);
  }
}
